<template>

  <ol>
    <li v-for="item in todo" v-bind:key="item.index" class="taskShowLi">
      <div class="taskNameContainer">
        <div class="taskName">{{item.taskName}}</div>
        <div class="deleteBtn">
          <button class="btn btn-danger" v-on:click="delete1(item.index)">删除</button>
        </div>
        <div class="clear"></div>
      </div>

    </li>
  </ol>


</template>
<script>
  export default {
    props: {
      todo: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    },
    methods: {
      delete1: function (index) {
        this.$emit("deleteTask", index);
      }
    }
  }
</script>

<style lang="scss" scoped>

.taskShowLi {
  position: relative;
  margin: 30px;

  .taskNameContainer {
    position: absolute;
    top: -15px;
    height: 50px;
    line-height: 50px;
    .taskName {
      width: 300px;
      overflow-x: hidden;
      float: left;
    }

    .deleteBtn {
      float: left;
    }

    .clear {
      clear: both;
    }
  }
}
</style>



