<template>
  <div id="textInputDiv">
    <input type="text" v-model="taskName" @keydown.enter="enter()" placeholder="输入任务名称,按回车确认" class="form-control"/>
  </div>

</template>


<script>
  export default {
    props:[],
    data(){
     return {
       taskName:''
     }
    },
    methods:{
      enter: function () {
        console.log(this.taskName);
        this.$emit('enterTask',this.taskName);
        this.taskName='';
      }
    }
  }
</script>
<style>
  #textInputDiv{
    padding-top: 40px;
    width: 500px;
    margin: 0 auto;
  }
</style>




