<template>
  <div class="container">
    <h2 class="text-center">Vue Todo List</h2>
    <BaseInputText v-on:enterTask="enter"/>
    <div v-if="todos.length" id="showList">
      <ListShow :todo="todos" v-on:deleteTask="deleteTask"/>
    </div>

  </div>

</template>

<script>
  import BaseInputText from './components/BaseInputText.vue';
  import ListShow from './components/ListShow.vue';

  export default {
    components: {BaseInputText, ListShow},
    name: 'app1',
    data() {
      return {
        todos: [{'taskName': "task1", 'index': 0}]
      }
    },
    methods: {
      enter: function (name) {
        if (this.todos.length) {
          this.todos.push({"taskName": name, "index": this.todos[this.todos.length - 1].index + 1});
        }
        else {
          this.todos.push({"taskName": name, "index": 0});
        }


      },
      deleteTask: function (index) {
        this.todos = this.todos.filter(function (item) {
          if (item.index !== index) {
            return true;
          }
        })
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }

  #showList {
    width: 500px;
    padding: 20px;
    margin: 40px auto;
    border: 1px solid #ddd;
  }
</style>



